import { Helmet } from "react-helmet";

import MainSlider from "../../components/MainSlider/MainSlider";
import ProductsSection from "../../components/ProductsSection/ProductsSection";
import AdvertisementList from "../../components/AdvertisementList/AdvertisementList";

const availableAdvertisements = [
	{
		title: (
			<>
				Discounts up to 30% <br /> on Segways
			</>
		),
		imgSrcWebp: "https://sentator.github.io/SmartTechnics-build/main-discounts/01.webp",
		imgSrcDefault: "https://sentator.github.io/SmartTechnics-build/main-discounts/01.png",
	},
	{
		title: (
			<>
				A week of <br /> smart watches
			</>
		),
		imgSrcWebp: "https://sentator.github.io/SmartTechnics-build/main-discounts/02.webp",
		imgSrcDefault: "https://sentator.github.io/SmartTechnics-build/main-discounts/02.png",
	},
	{
		title: (
			<>
				Sale <br /> up to -50%
			</>
		),
		imgSrcWebp: "https://sentator.github.io/SmartTechnics-build/main-discounts/03.webp",
		imgSrcDefault: "https://sentator.github.io/SmartTechnics-build/main-discounts/03.png",
	},
	{
		title: (
			<>
				Smart Balance Premium <br /> sweety prices
			</>
		),
		imgSrcWebp: "https://sentator.github.io/SmartTechnics-build/main-discounts/04.webp",
		imgSrcDefault: "https://sentator.github.io/SmartTechnics-build/main-discounts/04.png",
	},
];

const HomePage = () => {
	return (
		<>
			<Helmet>
				<title>Homepage - Smart Technics</title>
				<meta
					name="description"
					content={
						"You can find top-rated gyroscooters, available discount proposals and many other interestin on this page"
					}
				/>
			</Helmet>
			<main>
				<div className="container">
					<MainSlider />
					<ProductsSection title="Best Sellers" />
					<AdvertisementList availableAds={availableAdvertisements} showFromIndex={0} showToIndex={1} />
				</div>
			</main>
		</>
	);
};

export default HomePage;
