import AdvertisementItem from '../AdvertisementItem/AdvertisementItem';

import './advertisementList.scss';

const AdvertisementList = ({availableAds, showFromIndex, showToIndex}) => {
    return (
        <section className="main-discounts">
            <div className="container">
                <ul className="main-discounts__box">
                    {availableAds.length > 0
                        ? availableAds.map((item, i) => i === showFromIndex || i === showToIndex ? <AdvertisementItem key={i} title={item.title} imgSrcWebp={item.imgSrcWebp} imgSrcDefault={item.imgSrcDefault}/> : null)
                        : null
                    }
                </ul>
            </div>
        </section>
    )
}

export default AdvertisementList;