import { Link } from 'react-router-dom';

import './advertisementItem.scss';

const AdvertisementItem = ({title, imgSrcWebp, imgSrcDefault}) => {
    return (
        <li className="main-discounts__item item-discount">
            <Link className="item-discount__link" to="/catalogue">
                <h5 className="item-discount__title">{title}</h5>
                <picture className="item-discount__img">
                    <source srcSet={imgSrcWebp} type="image/webp"/>
                    <img className="item-discount__img" src={imgSrcDefault} alt={title}/>
                </picture>
            </Link>
        </li>
    )
}

export default AdvertisementItem;