import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Autoplay } from "swiper";
import { Link } from "react-router-dom";

import "swiper/scss";
import "swiper/scss/navigation";
import "swiper/scss/pagination";
import "swiper/css/effect-fade";
import "./mainSlider.scss";
import slide1 from "../../assets/images/main-slider/01.jpg";
import slide1Webp from "../../assets/images/main-slider/01.webp";
import slide2 from "../../assets/images/main-slider/02.jpg";
import slide2Webp from "../../assets/images/main-slider/02.webp";
import slide3 from "../../assets/images/main-slider/03.jpg";
import slide3Webp from "../../assets/images/main-slider/03.webp";
import slide1WebpMobile from "../../assets/images/main-slider/mobile/01.webp";
import slide2WebpMobile from "../../assets/images/main-slider/mobile/02.webp";
import slide3WebpMobile from "../../assets/images/main-slider/mobile/03.webp";

const slides = [
	{
		url: "/products/p0005",
		images: {
			webp: slide2Webp,
			webpMobile: slide2WebpMobile,
			src: slide2,
		},
	},
	{
		url: "/products/p0004",
		images: {
			webp: slide1Webp,
			webpMobile: slide1WebpMobile,
			src: slide1,
		},
	},
	{
		url: "/products/p0008",
		images: {
			webp: slide3Webp,
			webpMobile: slide3WebpMobile,
			src: slide3,
		},
	},
	{
		url: "/products/p0006",
		images: {
			webp: slide2Webp,
			webpMobile: slide2WebpMobile,
			src: slide2,
		},
	},
	{
		url: "/products/p0009",
		images: {
			webp: slide1Webp,
			webpMobile: slide1WebpMobile,
			src: slide1,
		},
	},
];

const MainSlider = () => {
	return (
		<div className="main-slider">
			<Swiper
				className="main-slider__swiper"
				modules={[Pagination, Autoplay]}
				spaceBetween={15}
				slidesPerView={1}
				allowTouchMove
				loop
				speed={1000}
				autoplay={{
					delay: 5000,
					disableOnInteraction: false,
				}}
				pagination={{
					clickable: true,
				}}
			>
				{slides.map(({ url, images }) => (
					<SwiperSlide key={url} className="main-slider__slide">
						<Link className="main-slider__slide-link" to={url} aria-label="go to a product page">
							<picture className="main-slider__slide-img">
								<source srcSet={images.webp} type="image/webp" media="(min-width: 769px)" />
								<source srcSet={images.webpMobile} type="image/webp" media="(max-width: 768px)" />
								<img className="main-slider__slide-img" src={images.src} alt="promo slide" />
							</picture>
						</Link>
					</SwiperSlide>
				))}
			</Swiper>
		</div>
	);
};

export default MainSlider;
